body {
  background-color: #c3cfe2;
  font-family: sans-serif;
  width: 500px;
  display: grid;
  grid-template-columns: repeat(3, 200px);
  grid-template-rows: repeat(3, 200px);
}
body .testimonial {
  background-color: #fff;
  padding: 30px;
  padding-bottom: 60px;
  margin: 10px;
  margin-bottom: 0px;
  border-radius: 15px 15px 0px 0px;
  grid-row: 1/span 1;
  grid-column: 1/span 3;
}
body .testimonial p {
  font-size: 20px;
  line-height: 35px;
}
body figure {
  grid-row: 2/span 1;
  grid-column: 2/span 2;
}
body figure img {
  height: 150px;
  width: 150px;
  border: 5px #fff solid;
  border-radius: 100%;
  object-fit: cover;
}
body footer {
  grid-row: 3/span 3;
  grid-column: 1/span 1;
  background-color: #686de0;
  margin: 10px;
  margin-top: 0px;
  padding: 30px;
  padding-top: 60px;
  border-radius: 0px 0px 15px 15px;
}
body footer .name {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
}
body footer a {
  color: #c3cfe2;
  text-decoration: none;
}

